import React, { PropsWithChildren } from 'react';
import { Grid, styled, Typography, useTheme } from '@mui/material';
import History from './History';
import { format } from 'date-fns';

interface Props {
  title: string;
  role: string;
  description: string;
  startDate: Date;
  endDate?: Date;
  link: string;
}
const OrganizationInfo = ({
  title,
  description,
  startDate,
  endDate,
  role,
  link,
  children,
}: PropsWithChildren<Props>) => {
  const { spacing, typography, palette } = useTheme();
  const formattedStartDate = format(startDate, 'yyyy.MM');
  const formattedEndDate = endDate != null ? format(endDate, 'yyyy.MM') : '현재';

  return (
    <Grid
      container
      sx={{
        marginBottom: spacing(6),
      }}
    >
      <Grid item xs={10}>
        <Link href={link} target="_blank" rel="noopener noreferrer">
          <Typography
            component="h3"
            fontFamily="Noto Sans"
            fontWeight={typography.fontWeightBold}
            fontSize={typography.h6.fontSize}
            color={palette.text.primary}
          >
            {title}
          </Typography>
        </Link>
        <Typography fontSize={typography.caption.fontSize} fontFamily="Noto Sans">
          {role}
        </Typography>
        <Typography variant="caption" color={palette.text.secondary} fontFamily="Noto Sans">
          {formattedStartDate} ~ {formattedEndDate}
        </Typography>
        <Typography fontSize={typography.body2.fontSize} sx={{ marginTop: spacing(1), marginBottom: spacing(1) }}>
          {description}
        </Typography>
        <div>{children}</div>
      </Grid>
    </Grid>
  );
};

const Link = styled('a')({
  textDecoration: 'none',
  transition: 'opacity .2s ease-in-out',
  '&:hover': {
    opacity: 0.6,
  },
});

OrganizationInfo.History = History;

export default OrganizationInfo;
