import React, { ReactNode } from 'react';
import { Box, Typography, Divider, useTheme } from '@mui/material';

interface Props {
  title: string;
  description?: ReactNode;
}
const SectionTitle = ({ title, description }: Props) => {
  const { spacing, palette, typography } = useTheme();

  return (
    <Box sx={{ marginTop: spacing(10) }}>
      <Typography variant="h4" component="h2" fontWeight={typography.fontWeightBold} gutterBottom>
        {title}
      </Typography>
      {description && <Typography>{description}</Typography>}
      <Divider
        sx={{
          width: '50%',
          backgroundColor: palette.grey[500],
          height: 1,
          margin: `${spacing(1)} 0 ${spacing(6)}`,
        }}
      />
    </Box>
  );
};

export default SectionTitle;
