import { graphql, useStaticQuery } from 'gatsby';

export function useCompanyLogo() {
  return useStaticQuery<Queries.CompanyLogoQuery>(graphql`
    query CompanyLogo {
      quotalab: file(absolutePath: { regex: "/logo/quotalab.png/" }) {
        childImageSharp {
          gatsbyImageData(width: 50, placeholder: BLURRED, formats: [AUTO])
        }
      }
      toss: file(absolutePath: { regex: "/logo/toss.png/" }) {
        childImageSharp {
          gatsbyImageData(width: 50, placeholder: BLURRED, formats: [AUTO])
        }
      }
      lubycon: file(absolutePath: { regex: "/logo/lubycon.png/" }) {
        childImageSharp {
          gatsbyImageData(width: 50, placeholder: BLURRED, formats: [AUTO])
        }
      }
      soomgo: file(absolutePath: { regex: "/logo/soomgo.png/" }) {
        childImageSharp {
          gatsbyImageData(width: 50, placeholder: BLURRED, formats: [AUTO])
        }
      }
      everyfriday: file(absolutePath: { regex: "/logo/everyfriday.jpeg/" }) {
        childImageSharp {
          gatsbyImageData(width: 50, placeholder: BLURRED, formats: [AUTO])
        }
      }
    }
  `);
}
