import React, { PropsWithChildren } from 'react';
import { Box, Typography, Grid, Divider, useTheme } from '@mui/material';
import SimplePostItem from 'components/SimplePostItem';
import { Link } from './models';
import ExternalLink from './ExternalLink';
import { Post } from 'models/fragments';

interface Props {
  title: string;
  links?: Link[];
  posts?: Post[];
}
const ProjectItem = ({ title, children, links = [], posts = [] }: PropsWithChildren<Props>) => {
  const { spacing, typography, palette, shape } = useTheme();

  return (
    <Box sx={{ marginBottom: spacing(6) }}>
      <Box>
        <Box sx={{ marginBottom: spacing() }}>
          <Grid container alignItems="center">
            <Grid item>
              <Typography
                fontWeight={typography.fontWeightBold}
                fontSize={typography.h6.fontSize}
                fontFamily="Noto Sans"
                component="h3"
              >
                {title}
              </Typography>
            </Grid>
            <Grid container item xs>
              {links.map(link => (
                <ExternalLink key={link.url} link={link} />
              ))}
            </Grid>
          </Grid>
        </Box>
        <Grid item xs={12} sx={{ marginBottom: spacing(4) }}>
          {children}
        </Grid>
        {posts.length > 0 && (
          <Box
            sx={{
              marginTop: spacing(2),
              border: `1px dashed ${palette.grey[400]}`,
              padding: spacing(2),
              borderRadius: shape.borderRadius,
            }}
          >
            {posts.map((post, index) => (
              <Box key={index}>
                <SimplePostItem post={post} />
                {index !== posts.length - 1 && (
                  <Box sx={{ margin: `${spacing(2)} 0` }}>
                    <Divider />
                  </Box>
                )}
              </Box>
            ))}
          </Box>
        )}
      </Box>
    </Box>
  );
};

const Description = ({ children }: PropsWithChildren<unknown>) => {
  const { typography } = useTheme();
  return <Typography variant="body2">{children}</Typography>;
};
ProjectItem.Description = Description;

export default ProjectItem;
